<template>
	<div class="d-box v-box">
		<div class="headDiv" style="padding: 20px;">
			<div class="headerLeft">
				<span class="system">品牌</span>
				<el-select v-model="form.brandCode" placeholder="请选择系统" >
					<el-option v-for="item in brandName" :key="item.brandcode" :label="item.dealername" :value="item.brandcode"></el-option>
				</el-select>
			</div>
			<div class="headerLeft">
				<span class="system">经销商全称</span>
				<el-input placeholder="请输入经销商全称" v-model="form.dealername" clearable>
				</el-input>
			</div>
			<div class="headerRight">
				<SolidButton :type='"solid"' :text='"查询"' @click="query"></SolidButton>
				<SolidButton :type='"hollow"' :text='"重置"' @click="reset"></SolidButton>
			</div>
			<div style="height: auto;width: 100%;padding:10px 0;float: left;">
				<Button :type='add.type' :icon='add.icon' :text='add.text' @click="addClick"></Button>
				<Button :type='edit.type' :icon='edit.icon' :text='edit.text' @click="editClick"></Button>
			</div>
		</div>
		<div class="flexTable">
			<div class="d-box v-box">
				<div class="flexTable spaceTable">
					<div class="tableDiv">
						<el-table ref="multipleTable" stripe :data="rows" tooltip-effect="dark" style="width: 100%;" height="100%" @selection-change="handleSelectionChange" @row-click="toggleSelection">
							<el-table-column type="selection" width="55">
							</el-table-column>
							<el-table-column prop="brandcode" label="品牌">
							</el-table-column>
							<el-table-column prop="dealername" label="经销商全称" show-overflow-tooltip>
							</el-table-column>
							<el-table-column prop="dealershtname" label="经销商简称" show-overflow-tooltip>
							</el-table-column>
							<el-table-column prop="addr" label="地址" show-overflow-tooltip>
							</el-table-column>
							<el-table-column prop="linkman1" label="联系人" show-overflow-tooltip>
							</el-table-column>
							<el-table-column prop="linktel1" label="联系电话" show-overflow-tooltip>
							</el-table-column>
							<el-table-column prop="prov" label="省份" show-overflow-tooltip>
							</el-table-column>
							<el-table-column prop="city" label="城市" show-overflow-tooltip>
							</el-table-column>
							<el-table-column prop="email" label="联系邮箱" show-overflow-tooltip>
							</el-table-column>
							<el-table-column prop="region" label="MDM经销商" show-overflow-tooltip>
								<template slot-scope="scope">
									<span style="margin-left: 10px" v-for="o in scope.row.dealerOmsList">{{o.dealerName}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><br />
								</template>
							</el-table-column>
							<el-table-column prop="status" label="状态" show-overflow-tooltip>
								<template slot-scope="scope">
									<span style="margin-left: 10px">{{scope.row.status?'有效':'无效'}}</span>
								</template>
							</el-table-column>

						</el-table>
					</div>
				</div>
				<div class="footerDiv">
					<el-pagination style="text-align: center;padding: 6px 0px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage1" :page-sizes="[10, 20, 30, 40]" :page-size="10" layout="total, sizes, prev, pager, next, jumper" :total="num">
					</el-pagination>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		components: {
			Button,
			SolidButton
		},
		data() {
			return {
				currentPage1: 0,
				name: '',
				phone: '',
				rows: [],
				num: 0,
				pageNum: '1',
				pageRows: '10',
				brandName: [],
				form: {
					brandCode: '',
					dealername: ''
				},
				list: '',
				//头部按钮
				add: {
					text: '新增', //按钮名称
					type: 'icon-add', //按钮颜色class,及图标
				},
				edit: {
					text: '编辑', //按钮名称
					type: 'icon-edit', //按钮颜色class,及图标
				},
			}
		},
		methods: {
			//表格Checkbox 选中
			handleSelectionChange(val) {
				this.list = val;
			},
			//点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//初始化数据
			loadData() {
				var self = this;
				var p = {
					type: "1",
					pageNum: this.pageNum,
					pageRows: this.pageRows,
					brandcode: this.form.brandCode,
					dealername: this.form.dealername,
				}
				//初始化列表
				self.$api.post('crm/dealer/queryCrmDealerList', p, r => {
					self.rows = r.repData.list
					self.num = r.repData.total
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
				//品牌下拉
				self.$api.post('/crm/dealer/getBrandCodes', {}, r => {
					self.brandName = r.repData
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})

			},
			handleSizeChange(val) {
				this.pageRows = val;
				this.loadData();
			},
			handleCurrentChange(val) {
				this.pageNum = val;
				this.loadData();
			},
			//新增
			addClick() {
				this.$router.push({
					path: "/DealerRevise",
					query: {}
				})
			},
			//编辑
			editClick() {
				if(this.list.length == 0) {
					this.$message({
						message: '至少选择一条',
						type: 'warning'
					});
					return;
				} else if(this.list.length > 1) {
					this.$message({
						message: '最多只能选择一条',
						type: 'warning'
					});
					return;
				}
				this.$router.push({
					path: "/DealerRevise",
					query: {
						list: this.list[0]
					}
				})
			},
			//查询
			query() {
				this.loadData();
			},
			//重置
			reset() {
				this.form.brandCode = '';
				this.form.dealername = '';
				this.loadData();
			}
		},
		created() {
			this.loadData();
		},
	}
</script>

<style scoped="scoped">
	header {
		padding: 20px;
		background: #fff;
		overflow: hidden;
	}
	
	.headerLeft {
		width: 294px;
		height: 40px;
		float: left;
		padding: 10px 0;
		margin-right: 40px;
	}
	
	.headerLeft .el-input {
		width: 188px;
		height: 37px;
	}
	
	.el-select {
		width: 188px;
	}
	
	.system {
		display: inherit;
		line-height: 40px;
		width: 25%;
		float: left;
	}
	
	.headerRight {
		width: 200px;
		float: right;
	}
	
	.style1 {
		background: #D9524F;
		color: #fff;
	}
	
	.style2 {
		border-color: #D9524F;
		color: #D9524F;
	}
	/*2\列表头部按钮*/
	
	.headerButton {
		height: 70px;
		background: #fff;
		padding: 20PX;
		float: left;
	}
	
	.iconDiv {
		margin-left: 10px;
		float: left;
		width: 50px;
		height: 50px;
		text-align: center;
		line-height: 50px;
		border-radius: 50%;
	}
	
	.iconDiv i {
		width: 21px;
		height: 21px;
		font-size: 15px;
		color: white;
	}
	
	.iconTitle {
		font-size: 10px;
		line-height: 22px;
	}
	
	.icon-addColor {
		background: #418BCA;
	}
	
	.icon-editColor {
		background: #F4516B;
	}
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		/*padding: 20px;*/
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
</style>


